












import Vue from 'vue';

export default Vue.extend({
  name: 'BlogSearch',
  data() {
    return {
      searchText: '',
    };
  },
  methods: {
    handleInput(event: Event) {
      if (event.target) {
        this.searchText = (event.target as HTMLInputElement).value;
        this.$emit('input', this.searchText.toLowerCase());
      }
    },
  },
});
