
















import Vue from 'vue';

export default Vue.extend({
  name: 'BlogFilter',
  data() {
    return {
      types: ['all', 'tutorial', 'announcement', 'experience'],
      selected: 'all',
    };
  },
  methods: {
    select(type: string) {
      this.selected = type;
      this.$emit('select', type);
    },
  },
});
